import { ChangeDetectionStrategy, Component, ElementRef, OnInit, OnDestroy } from '@angular/core';
import { DashboardBridgeService } from './dashboard-bridge.service';

@Component({
  selector: 'c8y-widget-legacy',
  template: '',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WidgetComponent implements OnInit, OnDestroy {
  ng1Scope;
  config;

  constructor(
    private dashboardBridgeService: DashboardBridgeService,
    private el: ElementRef
  ) {}

  async ngOnInit() {
    this.ng1Scope = await this.dashboardBridgeService.instantiateComponent(this.config, this.el.nativeElement);
  }

  ngOnDestroy(): void {
    if (this.ng1Scope) {
      this.ng1Scope.$destroy();
    }
  }
}
